.cart {
  background: #FFFFFF;
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 100%;
  position: fixed;
  transition: transform 0.25s ease-in;
  top: 0;
  width: 345px;
  z-index: 20;
}
.cart__header {
  align-items: center;
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  font-size: 1.5em;
  justify-content: space-between;
  padding: 1.25rem 2rem;
}
.cart__close {
  width: 14px;
}
.cart__list {
  padding: 1em 1em 1em 2em;
  overflow-y: auto;
}
.cart__item {
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  padding: 0.5em 0;
  margin-top: 0.5em;
}
.cart__image {
  flex-grow: 2;
  margin-right: 1em;
  margin-top: -0.7em;
  margin-left: -0.5em;
  width: 250px;
}
.cart__cost {
  color: #FF5C00;
  font-weight: bold;
  margin-bottom: 0.5em;
}
.cart__remove {
  color: #000000;
  font-size: 0.7em;
  opacity: 0.4;
}
.cart__footer {
  align-items: center;
  border-top: 1px solid #EBEBEB;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: auto;
  padding: 1.25em 2em 2em;
  width: 100%;
}
.cart__subtotal {
  margin-bottom: 1em;
  font-size: 1.15em;
  font-weight: bold;
}
.cart__button {
  margin-bottom: 2em;
  width: 100%;
}
.cart__continue {
  font-size: 0.8em;
  font-weight: bold;
}
.cart--active {
  transform: translateX(-100%);
  transition: transform 0.2s ease-out;
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  .cart {
    width: 425px;
  }
  .cart__header {
    font-weight: bold;
    padding: 1.25rem 2.5rem;
  }
  .cart__list {
    padding: 0.5em 2em;
  }
  .cart__item {
    padding: 1em 0;
    margin-top: 1em;
  }
  .cart__image {
    margin-right: 1em;
    margin-top: -0.7em;
    width: 275px;
  }
  .cart__cost {
    margin-bottom: 1em;
  }
  .cart__footer {
    padding: 1.25em 3em 2.5em;
  }
}